import React from 'react';
import PlayerCounter from './PlayerCounter';
import '../css/PlayerInfo.css';

const PlayerInfo = ({
	player,
	currentPlayerId
}) => {
	const extraClasses = (player.id === currentPlayerId) ? 'currentPlayer' : '';
	
	return (
		<li id={player.id} className={'playerInfo ' + extraClasses}>
			<div className='playerName'>Name: {player.name}</div>
			<div className='playerCounterHolder'>
				<PlayerCounter player={player}/>
			</div>
		</li>
	);
}
export default PlayerInfo;


