import React from 'react'
import { connect } from 'react-redux';
import * as actions from '../actions';
import { getItem, getCanInsertSpareSquare, getDisabledBoardButtonId } from '../reducers';
import Square from '../components/Square';
import BoardButton from '../components/BoardButton';

class ItemContainer extends React.Component {
	componentDidMount() {
		//this.fetchData();

	}

	componentDidUpdate(prevProps) {}

	render() {
		let itemToDisplay;
		const { item } = this.props;
		switch(item.type) {
			case "square":
				itemToDisplay = <Square {...item} />
				break;
			case "boardButton":
				const { insertSpareSquare, canInsert, disabledBoardButtonItem } = this.props;
				const isItemDisabled = (!canInsert || item.id === disabledBoardButtonItem)
				itemToDisplay = <BoardButton {...item} onButtonClick={insertSpareSquare} disabled={isItemDisabled}/>
				break;
			default:
				itemToDisplay = <div className='boardItem'/>;
				break;
		}
		return itemToDisplay;
	}
}


const mapStateToProps = (state, props) => {
	return {
		item: getItem(state, props.itemId),
		canInsert: getCanInsertSpareSquare(state),
		disabledBoardButtonItem: getDisabledBoardButtonId(state),
	}
};

ItemContainer = connect(
	mapStateToProps,
	actions
)(ItemContainer);

export default ItemContainer


