import React from 'react'
import { connect } from 'react-redux';
import * as actions from '../actions';
import { getNextCardForCurrentPlayer } from '../reducers';
import SquareImage from '../components/SquareImage';
import '../css/nextSquareContainer.css';

class NextSquareContainer extends React.Component {
	componentDidMount() {
		//this.fetchData();

	}

	componentDidUpdate(prevProps) {}

	render() {
		const {nextCard} = this.props;
		if(nextCard) {
			return (
				<div className="nextCardHolder">
	                	<SquareImage {...nextCard} />
	            </div>
			);
		} else return null;
		
	}
}


const mapStateToProps = (state, props) => {
	return {
		nextCard: getNextCardForCurrentPlayer(state),
	}
};

NextSquareContainer = connect(
	mapStateToProps,
	actions
)(NextSquareContainer);

export default NextSquareContainer


